.imageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 10;
}

.bkIndex{
  z-index: -1;
}

.image {
  width: auto;
  height: 100%;
  object-fit: cover;
  position: absolute; /* Z축에서 쌓이도록 설정 */
  max-width: 100vw;
  max-height: 100vh;
}
